<template lang="html">
  <div class="">
    <v-snackbar
        v-model="snackbar"
        :timeout="1500"
      >
        Success!
        <v-btn
          color="blue"
          text
          @click="snackbar = false"
        >
          Close
        </v-btn>
      </v-snackbar>
    <v-card
      max-width="640"
      class="mx-auto"
      style="margin-top: 70px"
    >
      <v-card-title>прибыль</v-card-title>
      <v-card-text>
        <v-text-field v-model="name" label="имя" />
        <div>
          <div v-for="({name, value, persent}, i) in list" style="display: flex;width: 100%" :key="i">
            <span style="flex: 2">{{name}}</span>
            <span>{{value}} {{persent ? '%' : 'Р'}}</span>
            <v-icon style="cursor: pointer" @click="list.splice(i,1)">
              close
            </v-icon>
          </div>
          <div v-if="list.length" class="">
            <hr/>
            <div class="">
              <span style="float: right">TOTAL: {{total}} Р</span>
            </div>
          </div>
        </div>
        <v-text-field v-model="temp" @keyup.enter="enter" label="позиция = цена (р)" />
      </v-card-text>
      <v-card-actions>
        <v-btn :disabled="cantcreate" @click="create" text>Создать</v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  methods: {
    enter(){
      let [ name , value ] = this.temp.split('=')
      let persent = false

      if ( /%/.test(value) ) {
        persent = true
        value = value.replace( /%/g, '' )
      }

      if ( name && !isNaN(parseInt(value)))
        this.list.push({
          name, value: parseInt(value), persent
        })

      this.temp = ""
    },
    create(){

    }
  },
  computed: {
    cantcreate(){
      return false
    },
    total(){
      const vs = this.list.filter( ({ persent }) => !persent ).reduce((acc, {value}) => acc+value,0)
      const p = this.list.filter(  ({ persent }) => persent ).reduce((acc, {value}) => acc+value/100,0)
      return ~~(vs * (1 + p) )
    }
  },
  data: () => ({
    snackbar: false,
    list: [],
    name: "",
    temp: ""
  })
}
</script>

