<template lang="html">
  <div>
    <v-card
      max-width="640"
      class="mx-auto"
      style="margin-top: 70px"
    >
      <v-card-title>кредит</v-card-title>
      <v-card-text>
        <v-text-field label="Имя кредита" v-model="name" />
        <v-text-field label="Сумма займа" v-model="sum" />
        <v-text-field label="Cрок займа (мес)" v-model="time" />
        <v-slider :label="`проц ставка ${persent.toFixed(2)}%`" step="0.1" v-model="persent" />
        <p>
          В месяц: {{bill}} | Переплата: {{ overhead }}
        </p>
        <v-menu
          v-model="start"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="date"
              label="дата взятия кредита"
              prepend-icon="event"
              readonly
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="date" @input="start = false"></v-date-picker>
        </v-menu>
        <p>тип платежей: {{ diff ? 'дифференцированный' : 'аннуитетный' }}</p>
        <v-switch label="дифференцированный" v-model="diff" />
          <v-expansion-panels>
            <v-expansion-panel>
              <v-expansion-panel-header>Календарь платежей</v-expansion-panel-header>
              <v-expansion-panel-content>
                <p>
                  <span v-for="({start, amount}) in children">
                    <b>{{amount}} Р</b> <span style="float: right">{{start | toTime}}</span>
                    <br/>
                  </span>
                </p>
              </v-expansion-panel-content>
            </v-expansion-panel>
        </v-expansion-panels>
      </v-card-text>
      <v-card-actions>
        <v-btn :disabled="cantcreate" @click="create" text>Создать</v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import moment from 'moment'

export default {
  filters: {
    toTime(value){
      return moment(value).format('DD MMMM YYYY')
    }
  },
  methods: {
    create(){
      this.$store.commit( 'addtool', {
        name: this.name,
        start: this.date,
        children: Array.from(this.children)
      })
    },
    calcBill( index ){
      const curr = moment(this.date).add(index, 'M')
      const prop = curr.daysInMonth() / 365
      const body = this.sum / this.time
      const per = ( this.sum - body * index ) * prop * (this.persent / 100 )
      return ~( -1 *  ( body + per ) * 100)/100
    }
  },
  data: () => ({
    name: "",
    diff: false,
    sum: 0,
    time: 0,
    persent: 9.9,
    date: new Date().toISOString().substr(0, 10),
    start: false
  }),
  computed: {
    children(){
      return new Array( parseInt(this.time) + 1 ).fill(0).map((_,i) => ({
        start: parseInt( moment(this.date).add(i, 'M').format('x') ),
        amount: i ?
          parseFloat( this.diff ?
            this.calcBill( i ) * -1 : this.bill * -1
          ) : parseFloat( this.sum )
      }))
    },
    bill(){
      // if annu
      const st = (this.persent / 100 ) / 12
      const part = Math.pow( 1+st , this.time )
      const k = st * part / ( part - 1 || 0 )
      return ~~ ( k*this.sum * 100 ) / 100
      // if diff = this.sum - this.sum / this.time * index + this.sum - this.sum / this.time * index * persents (31/365 * 20%)
    },
    overhead(){
      return (
        Math.abs( this.children.filter(({amount}) => amount < 0).reduce( (acc, { amount }) => acc + amount , 0 ) ) - this.sum).toFixed(2)
    },
    cantcreate(){
      return !(this.name && this.persent > 0 && this.sum > 0 && this.time > 0)
    }
  }
}
</script>

