<template lang="html">
  <div>
    <v-snackbar
        v-model="snackbar"
        :timeout="1500"
      >
        Success!
        <v-btn
          color="blue"
          text
          @click="snackbar = false"
        >
          Close
        </v-btn>
      </v-snackbar>
    <v-card
      max-width="640"
      class="mx-auto"
    >
      <v-card-title>платеж</v-card-title>
      <v-card-text>
        <v-text-field v-model="name" label="имя" />
        <v-text-field
  single-line
  type="number" v-model="amount" label="Cумма" />
        Расход <v-switch
        v-model="minus"
        :label="`это ${minus ? 'расход' : 'прибыль' }`"
        ></v-switch>
        <v-menu
          v-model="start"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="date"
              label="дата первого платежа"
              prepend-icon="event"
              readonly
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="date" @input="start = false"></v-date-picker>
        </v-menu>
        <div style="display: flex">
          <v-select
              v-model="repeat"
              :items="repeats"
              menu-props="auto"
              label="Повтор платежа"
              hide-details
              prepend-icon="date_range"
              single-line
            ></v-select>
            <v-icon style="cursor: pointer" @click="norepeat">
              close
            </v-icon>
        </div>

        повтор: несколько дат или раз в (час/день/неделя/мес/год и тд) или через
        <br/>
      </v-card-text>
      <v-card-actions>
        <v-btn :disabled="cantcreate" @click="make" text>Создать</v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  computed: {
    cantcreate(){
        return !(this.amount && this.name)
    },
    repeats(){
      return this.$store.getters.repeats
    }
  },
  methods: {
    make(){
      this.snackbar = true
      const {
        name, amount, date, repeat, minus
      } = this

      this.$store.commit( 'addevent' ,
        { name, amount: amount * (minus?-1:1), start: date, repeat }
      )
      this.name = ""
      this.amount = ""
      this.repeat = null
    },
    norepeat(){
      this.repeat = null
    }
  },
  data: () => ({
    snackbar: false,
    minus: false,
    repeat: null,
    date: new Date().toISOString().substr(0, 10),
    menu: false,
    modal: false,
    start: false,
    name: "",
    amount: 0
  }),
}
</script>
